import { DragDropContext, Draggable, Droppable, DropResult } from "react-beautiful-dnd";
import { useRecoilState, useRecoilValue } from "recoil";
import styled from "styled-components";
import { categoryState, IToDoState, toDoState } from "./atoms";
import Board from "./components/Board";
import CategoryForm from "./components/CategoryForm";
import DraggableTrash from "./components/DraggableTrash";

const Wrapper = styled.div`
  display: flex;
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
`;

const Boards = styled.div`
  display: grid;
  width: 100%;
  grid-template-columns: repeat(3, 3fr);
  gap: 10px;
  padding: 1rem;
`;

function App() {
  const [category, setCategory] = useRecoilState(categoryState);
  const [toDos, setToDos] = useRecoilState(toDoState);

  /**
   *       
   */
  const onDragEnd = (info: DropResult) => {
    const {destination, source, type} = info;
    if(!destination) return; // destination undefined   .

    //   
    if(type === "board"){
      setCategory(prev => {
        const copy = [...prev];
        const sourceStr = copy[source.index]; //  
        const destinationStr = copy[destination.index]; //  
        copy.splice(source.index, 1, destinationStr);
        copy.splice(destination?.index, 1, sourceStr);
        return [...copy];
      });

    //   ()
    }else if(destination?.droppableId === "trash"){
      const tmpText = toDos[source.droppableId][source.index].text;
      if(window.confirm(`'${[source.droppableId]}' '${tmpText}'  ?`)){
        setToDos((allBoards) => {
          const sourceBoard = [...allBoards[source.droppableId]];
          sourceBoard.splice(source.index, 1);
          return {
            ...allBoards,
            [source.droppableId]: sourceBoard
          }
        });
      }      

    //    
    }else if(destination?.droppableId === source.droppableId){      
      // 1.    .
      setToDos((allBoards) => {
        const boardCopy = [...allBoards[source.droppableId]];
        const taskObj = boardCopy[source.index];
        //1) source.index   copy array .
        //2) destination.index    draggableId 
        boardCopy.splice(source.index, 1); //1);
        boardCopy.splice(destination?.index, 0, taskObj); //2);
        //return     board  board return.
        return {
          ...allBoards,
          [source.droppableId]: boardCopy
        };
      });

    //    
    }else if(destination?.droppableId !== source.droppableId){
      setToDos((allBoards) => {
        const sourceBoard = [...allBoards[source.droppableId]];
        const taskObj = sourceBoard[source.index];
        const destinationBoard = [...allBoards[destination.droppableId]];

        sourceBoard.splice(source.index, 1);
        destinationBoard.splice(destination.index, 0, taskObj);

        return {
          ...allBoards,
          [source.droppableId]: sourceBoard,
          [destination.droppableId]: destinationBoard
        }
      });
    }    
  } 

  return (
    <Wrapper>
      <CategoryForm />
        <DragDropContext onDragEnd={onDragEnd}>
          <Droppable droppableId="boards" direction="horizontal" type="board">
            {(magic) => (
              <>
                <Boards ref={magic.innerRef} {...magic.droppableProps}>
                  {category.map((boardId, index) => (                  
                      <Board 
                        key={boardId} 
                        index={index}
                        toDos={toDos[boardId]} 
                        boardId={boardId}                    
                      />                      
                    ))
                  }          
                </Boards>
                <DraggableTrash />
                {magic.placeholder}
              </>              
            )}
          </Droppable>
      </DragDropContext>
    </Wrapper>    
  );
}

export default App;